/* App.css */
html, body, #root {
  height: 100%;
  margin: 0;
  font-family: Arial, sans-serif;
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header {
  background-color: #282c34;
  color: white;
  padding: 20px;
  text-align: center;
}

main {
  flex: 1;
  padding: 20px;
  background-color: #f9f9f9;
  text-align: center;
}

footer {
  background-color: #ddd;
  padding: 10px;
  text-align: center;
}
